<template>
    <div class="m-dashboard m-dashboard-work m-dashboard-fav">
        <h2 class="u-title">我的收藏</h2>
        <el-input
            class="m-dashboard-work-search"
            placeholder="请输入搜索内容"
            v-model="search"
            @change="searchPost"
        >
            <template slot="prepend">关键词</template>
            <el-button
                slot="append"
                icon="el-icon-search"
                @click="searchPost"
            ></el-button>
        </el-input>

        <div class="m-dashboard-box">
            <ul class="m-dashboard-box-list" v-if="data.length">
                <li v-for="(item, i) in data" :key="i">
                    <i class="u-icon">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" aria-hidden="true" fill="#555" v-if="item.post_publish" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 .55-.45 1-1 1H6v2l-1.5-1.5L3 16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 1-1h10c.55 0 1 .45 1 1zm-1 10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"/></svg>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" fill="#dbab09" v-else="" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 13H3v-1h1v1zm8-6v7c0 .55-.45 1-1 1H1c-.55 0-1-.45-1-1V7c0-.55.45-1 1-1h1V4c0-2.2 1.8-4 4-4s4 1.8 4 4v2h1c.55 0 1 .45 1 1zM3.8 6h4.41V4c0-1.22-.98-2.2-2.2-2.2-1.22 0-2.2.98-2.2 2.2v2H3.8zM11 7H2v7h9V7zM4 8H3v1h1V8zm0 2H3v1h1v-1z"/></svg>
                    </i>
                    <a
                        class="u-title"
                        target="_blank"
                        :href="getLink(item.post_type, item.post_id)"
                        >{{ item.post_title || "无标题" }}</a
                    >
                    <div class="u-desc">
                        发布于: {{ item.post_created * 1000 | dateFormat }} |
                        最后更新: {{ item.post_updated * 1000 | dateFormat }}
                    </div>
                    <el-button-group class="u-action">
                        <el-button
                            size="mini"
                            icon="el-icon-delete"
                            title="删除"
                            @click="del(item.post_type, item.post_id)"
                        ></el-button>
                    </el-button-group>
                </li>
            </ul>
            <el-alert
                v-else
                class="m-dashboard-box-null"
                title="没有找到相关条目"
                type="info"
                center
                show-icon
            >
            </el-alert>
            <el-pagination
                class="m-dashboard-box-pages"
                background
                :hide-on-single-page="true"
                @current-change="page_change"
                :current-page="page"
                layout="total, prev, pager, next, jumper"
                :total="total"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
import { getMyFavs, delFav } from "../service/fav";
import { getLink } from "@jx3box/jx3box-common/js/utils";
import dateFormat from "../utils/dateFormat";

export default {
    name: "fav",
    props: [],
    data: function() {
        return {
            type: "all",
            data: [],
            total: 1,
            page: 1,
            search: "",
        };
    },
    computed: {},
    methods: {
        page_change(i = 1) {
            getMyFavs({
                keyword: this.search,
                page: i,
            }).then((res) => {
                res = res.data;
                this.data = res.code === 200 ? res.data.data : [];
                this.total = res.code === 200 ? res.data.total : 0;
            });
        },
        searchPost() {
            this.page_change(1);
        },
        del: function(type, id) {
            this.$alert("确定要取消收藏吗？", "确认信息", {
                confirmButtonText: "确定",
                callback: (action) => {
                    delFav(type, id).then(() => {
                        this.$message({
                            type: "success",
                            message: `取消收藏成功`,
                        });
                        location.reload();
                    });
                },
            });
        },
        getLink,
    },
    filters: {
        dateFormat: function(val) {
            return dateFormat(new Date(val));
        },
    },
    mounted: function() {
        this.page_change();
    },
};
</script>

<style scoped lang="less">
@import "../assets/css/work.less";
</style>
